import React, { useCallback, useRef } from 'react';
import { RiMailLine, RiUser6Line } from 'react-icons/ri';
import * as Yup from 'yup';

import { FormHandles } from '@unform/core';
import { Form } from '@unform/web';

import Button from '../../components/Button';
import Input from '../../components/Input';
import getValidationErrors from '../../utils/getValidationErrors';
import { LandingFormData } from '../../utils/interfaces';
import Cards from './Cards';
import Footer from './Footer';
import Hero from './Hero';
import Main from './Main';
import Partners from './Partners';

import { Container } from './styles';

const Landing: React.FC = () => {
  const formRef = useRef<FormHandles>(null);

  const handleSubmit = useCallback(async (data: LandingFormData) => {
    try {
      formRef.current?.setErrors({});
      const schema = Yup.object().shape({
        name: Yup.string().matches(
          /(^$|^['-\sA-Za-z\u00c0-\u024f\u1e00-\u1eff]+$)/,
          'This name does not look right',
        ),
        email: Yup.string()
          .required('We need your e-mail')
          .email('We need a valid e-mail'),
      });
      await schema.validate(data, { abortEarly: false });
      // TODO Something should happen here
    } catch (error) {
      if (error instanceof Yup.ValidationError) {
        const validationErrors = getValidationErrors(error);
        formRef.current?.setErrors(validationErrors);
      }
    }
  }, []);

  return (
    <Container>
      <Hero />
      <Main>
        <blockquote>
          ‚ÄúIf you do not know where you come from, then you don‚Äôt know where you
          are, and if you don‚Äôt know where you are, then you don‚Äôt know where
          you‚Äôre going. And if you don‚Äôt know where you‚Äôre going, you‚Äôre
          probably going wrong.‚Äù
          <strong>Terry Pratchett, I Shall Wear Midnight</strong>
        </blockquote>
        <hr />
        <h3>
          We can help you begin your journey towards the best version of you.
        </h3>
        <Form ref={formRef} onSubmit={handleSubmit}>
          <Input
            name="name"
            icon={RiUser6Line}
            placeholder="My name (optional)"
            className="input"
          />
          <Input
            name="email"
            icon={RiMailLine}
            placeholder="My e-mail"
            className="input"
          />
          <Button type="submit">Send</Button>
        </Form>
        <hr />
        <Cards />
      </Main>
      <Partners />
      <Footer />
    </Container>
  );
};

export default Landing;
